function Regex(string) {
    const tamanho = string.length
    if (tamanho <= 3) {
        return [/(\d{1,3})$/, "$1"]
    } else if (tamanho <= 6) {
        return [/(\d{1,3})(\d{3})$/, "$1.$2"]
    } else if (tamanho <= 9) {
        return [/(\d{1,3})(\d{3})(\d{3})$/, "$1.$2.$3"]
    }
}

export default Regex