import { React } from 'react';
import Regex from '../../../services/Regex'
import { Typography, Box } from '@material-ui/core'

export default function Informacoes({ dados }) {

    let casosConfirmados = '';
    let vitimas = '';
    let mortesInfectados = 0;

    if (dados.hasOwnProperty('confirmed')) {

        casosConfirmados = dados.confirmed.toString()
        let regex = Regex(casosConfirmados)
        casosConfirmados = casosConfirmados.replace(regex[0], regex[1])

        vitimas = dados.deaths
        vitimas = dados.deaths.toString()
        regex = Regex(vitimas)
        vitimas = vitimas.replace(regex[0], regex[1])

        mortesInfectados = (dados.deaths / (dados.confirmed / 100)).toFixed(2)
    }

    return (
        <Box mt={3}>
            <Typography variant="h4" component="h1" align="left">País: {dados.country}</Typography>
            <Typography variant="h6" component="h2" align="left">Casos confirmados: {casosConfirmados}</Typography>
            <Typography variant="h6" component="h2" align="left">Mortos: {vitimas}</Typography>
            <Typography variant="h6" component="h2" align="left">Mortes/infectados: {mortesInfectados}%</Typography>
        </Box>
    )

}