import React, { useEffect, useState } from 'react';
import './AppCovid.css'
import construir from '../../../services/api';
import Informacoes from '../Informacoes/Informacoes.jsx';
import { InputLabel, Select } from '@material-ui/core';


export default function Selecao() {
    const [paises, setPaises] = useState([]);
    const [dados, setDados] = useState({});
    const [nomePais, setNomePais] = useState('');

    function pegarPaises(dadoslocais) {
        const objpaises = JSON.parse(dadoslocais)
        const paises = Object.keys(objpaises)
        setPaises(paises)
    }

    function verificarData() {
        let dataLocal = localStorage.getItem('Data')
        const dataAtual = Date.now()
        const diferenca = dataAtual - dataLocal
        if(diferenca >= 3600000) {
            return true
        } else {
            return false
        }
    }

    useEffect(() => {
        async function captarPaises() {

            const api = construir('https://covid-api.mmediagroup.fr/v1/cases')
            const resposta = await api.get()
            const respostaStrigfy = JSON.stringify(resposta.data)

            localStorage.setItem('Data', JSON.stringify(Date.now()))
            localStorage.setItem('Dados', respostaStrigfy);
            pegarPaises(respostaStrigfy)
        }

        let dadoslocais = localStorage.getItem('Dados')
        const recarregar = verificarData()
        recarregar ? captarPaises() : pegarPaises(dadoslocais)
        
    }, [])


    function handleChange(event) {
        const pais = event.target.value
        if(pais === '') {
            return
        }
        const dadoslocais = JSON.parse(localStorage.getItem('Dados'))
        const dados = dadoslocais[pais]
        setNomePais(pais)
        setDados(dados.All)
    }

    return (
        <div className="main">
            <div className="selecionarPais">
                <InputLabel htmlFor="select">Selecione um país</InputLabel>
                <Select
                    native
                    value={nomePais}
                    onChange={handleChange}
                    inputProps={{
                        name: "select",
                        id: "select",
                    }}
                >
                    <option value=''>-- --- --</option>
                    {
                        paises.map((pais, index) => {
                            return (
                                <option
                                    key={index}
                                >{pais}
                                </option>
                            )
                        })
                    }
                </Select>
            </div>
            <Informacoes dados={dados} />
        </div >
    )
}